import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


const state = {
    awsiot: {
        Auto: false,
        tc: "",
        t1: "",
        t2: "",
        t3: "",
        fan: "",
        servo: ""
    }
}

const mutations = {
    updateAWS(state, update) {
        state.awsiot = update
        console.log("State is now", state)
        console.log("awsiot: ", state.awsiot)
    },
    updateSwitch(state, button, switchPos) {
        button = switchPos;
        state.obj.button = button;
    },
}

const actions = {
    updateAWS({ commit }, newState) {
        console.log("Updating AWS State", newState)
        commit('updateAWS', newState)
    }
}

const getters = {
    auto: state => state.awsiot.auto,
    tc: state => `${state.awsiot.tc}C`,
    t1: state => `${state.awsiot.t1}C`,
    t2: state => `${state.awsiot.t2}C`,
    t3: state => `${state.awsiot.t3}C`,
    fanSpeed: state => state.awsiot.fan,
    servoAngle: state => state.awsiot.servo
}

export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations,
    strict: process.env.NODE_ENV !== 'production'
})