<template>
  <v-container>
    <v-btn
      v-if="!$auth.isAuthenticated && !$auth.loading"
      @click.prevent="login"
    >
      Login
    </v-btn>
    <!-- <v-btn v-if="$auth.isAuthenticated" class @click.prevent="logout">
    Logout
    </v-btn> -->

  </v-container>
</template>

<script>
export default {
  name: "Login",
  methods: {
    login() {
      this.$auth.loginWithRedirect();
    },
    logout() {
      this.$auth.logout();
      this.$router.push({ path: "/" });
    },
  },
};
</script>

