<template>
  <v-container fluid>
    <h1 class="text-xl font-weight-regular">Barbecue Control</h1>
    <v-divider></v-divider>
    <br />
    <v-row>
      <v-col>
        <v-card>
          <v-card-title>Auto Mode</v-card-title>
          <v-card-text>
            <v-switch
              colour = 'primary'
              v-model="auto"
              label="Auto Mode"
            ></v-switch>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col>
        <v-card v-if="!this.$store.state.awsiot.auto">
          <v-card-title>Fan Speed</v-card-title>
          <v-card-text>
            <v-switch
              colour = 'primary'
              v-model="fanSpeed"
              label="Fan Speed"
            ></v-switch>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col>
        <v-card v-if="!this.$store.state.awsiot.auto">
          <v-card-title>Servo Angle</v-card-title>
          <v-card-text>
            <v-switch
              colour = 'primary'
              v-model="servoAngle"
              label="Servo Angle"
            ></v-switch>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>



<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["tc", "t1", "t2", "t3", "fanSpeed", "servoAngle"]),
    auto: {
      get() {
        if (this.$store.state.awsiot.auto === undefined) {
          this.updateShadow("bbq-micro", "auto", false);
        }
        return this.$store.state.awsiot.auto;
      },
      set(value) {
        this.updateShadow("bbq-micro", "auto", value);
      },
    },
    fanSpeed: {
      get() {
        if (this.$store.state.awsiot.fanSpeed === undefined) {
          this.updateShadow("bbq-micro", "fanSpeed", false);
        }
        return this.$store.state.awsiot.fanSpeed;
      },
      set(value) {
        this.updateShadow("bbq-micro", "fanSpeed", value);
      },
    },
    servoAngle: {
      get() {
        if (this.$store.state.awsiot.servoAngle === undefined) {
          this.updateShadow("bbq-micro", "servoAngle", false);
        }
        return this.$store.state.awsiot.auto;
      },
      set(value) {
        this.updateShadow("bbq-micro", "auto", value);
      },
    },

    data() {
      return {};
    },
    methods: {},
  },
};
</script>